import React from "react";
import { Input } from "antd";
import "./styles.css";

const InputCommon = ({
  placeholder,
  defaultValue,
  type,
  onChange,
  requr,
  value,
}) => {
  return (
    <>
      <Input
        required={requr}
        defaultValue={defaultValue}
        placeholder={placeholder}
        type={type}
        onChange={onChange}
        value={value}
      />
    </>
  );
};

export default InputCommon;
