import React, { useEffect, useState } from "react";
import Cookies from "universal-cookie";
import { Layout, Menu, theme } from "antd";
import { useLocation, NavLink, useNavigate } from "react-router-dom";
import { Dropdown, Space } from "antd";
import { Switch } from "antd";
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  UsergroupAddOutlined,
  UserAddOutlined,
  AppstoreOutlined,
  ShoppingCartOutlined,
  FileImageOutlined,
  LoginOutlined,
  CommentOutlined,
  BellOutlined,
  QuestionCircleOutlined,
  UserOutlined,
  SettingOutlined,
  ClusterOutlined,
  ContainerOutlined,
  DatabaseOutlined,
  LayoutOutlined,
  UserSwitchOutlined,
  ShoppingOutlined,
  BoxPlotOutlined,
  BorderOuterOutlined,
  PhoneOutlined,
} from "@ant-design/icons";
import { Button, Tooltip } from "antd";
import "./styles.css";
const cookies = new Cookies();
const { Header, Content, Footer } = Layout;

const HorizontalSidebar = ({ children, setIsChecked }) => {
  const text = <span>Выйти</span>;

  const pathname = useLocation();
  const navigate = useNavigate();
  const HandleLogout = () => {
    cookies.remove("token");
    setTimeout(() => {
      navigate("/");
      window.location.reload();
    }, "1500");
  };

  const onChange = async (checked) => {
    window.localStorage.setItem("checked", "true");
    window.location.reload();
  };

  const {
    token: { colorBgContainer },
  } = theme.useToken();

  const items = [
    {
      label: (
        <Tooltip title={text}>
          <button className="tool_btns" onClick={HandleLogout}>
            <span>Выйти</span>
            <LoginOutlined />
          </button>
        </Tooltip>
      ),
      key: "0",
    },
    {
      label: (
        <div className="switch_wrapp">
          {" "}
          <span>Боковая панель</span> <Switch onChange={onChange} />
        </div>
      ),
      key: "1",
    },
  ];

  return (
    <Layout className="layout hori_sidebar">
      <Header
        style={{
          display: "flex",
          alignItems: "center",
        }}
      >
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={["2"]}
          items={[
            {
              key: "1",
              icon: <UserAddOutlined />,
              label: (
                <NavLink
                  to={"/adminadd"}
                  className={pathname == "/adminadd" ? "active" : ""}
                >
                  <span class="title">Админ</span>
                </NavLink>
              ),
            },
            {
              key: "2",
              icon: <ShoppingOutlined />,
              label: (
                <>
                  <span class="title">Продукты</span>
                </>
              ),
              children: [
                {
                  key: "3",
                  label: <NavLink to={"/products"}>Все продукты</NavLink>,
                },
                {
                  key: "4",
                  label: <NavLink to={"/brands"}>бренд</NavLink>,
                },
                {
                  key: "5",
                  label: <NavLink to={"/category"}>Категория</NavLink>,
                },
              ],
            },
            {
              key: "6",
              icon: <BoxPlotOutlined />,
              label: (
                <NavLink to={"/banner"}>
                  <span class="title">Баннер</span>
                </NavLink>
              ),
            },
            {
              key: "7",
              icon: <BorderOuterOutlined />,
              label: (
                <NavLink to={"/neworders"}>
                  <span class="title">Новые заказы</span>
                </NavLink>
              ),
            },
            {
              key: "8",
              icon: <ContainerOutlined />,
              label: (
                <NavLink to={"/gottenorders"}>
                  <span class="title">Заказы </span>
                </NavLink>
              ),
            },
            {
              key: "9",
              icon: <UserSwitchOutlined />,
              label: (
                <NavLink to={"/users"}>
                  <span class="title">Заявки </span>
                </NavLink>
              ),
            },
            {
              key: "9",
              icon: <CommentOutlined />,
              label: (
                <NavLink to={"/comments"}>
                  <span class="title">Комментарии </span>
                </NavLink>
              ),
            },
          ]}
        />
        <div className="profile">
          <Dropdown
            style={{ marginLeft: "10px" }}
            menu={{
              items,
            }}
            trigger={["click"]}
          >
            <a onClick={(e) => e.preventDefault()}>
              <Space>
                <SettingOutlined />
                <span className="drop_span">Настройки</span>
              </Space>
            </a>
          </Dropdown>
          <UserOutlined className="user-icon" />
          <div className="profile-right">
            {/* {filterData.map((elem) => (
              <>
                <p className="profile_name">{elem.name}</p>
                <p className="profile_email">{elem.email}</p>
              </>
            ))} */}
          </div>
        </div>
      </Header>
      <Content
        style={{
          padding: "0 50px",
        }}
      >
        <div
          className="site-layout-content"
          style={{
            background: colorBgContainer,
            borderRadius: "20px",
          }}
        >
          {children}
        </div>
      </Content>
      <Footer
        style={{
          textAlign: "center",
          background: "transparent",
          color: "rgb(31, 171, 138)",
          fontWeight: "500",
          padding: "15px 50px",
        }}
      >
        ©2023 Created by Kamronbey
      </Footer>
    </Layout>
  );
};
export default HorizontalSidebar;
