import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { Image, Input, Spin } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import "./styles.css";
import { BrandPost, BrandUpload } from "../../../redux/brands";
import TextArea from "antd/es/input/TextArea";
import {
  GetComments,
  PostComments,
  PutComments,
} from "../../../redux/comments";

function PutComment({ Open, HandleClose, put_id }) {
  const dispatch = useDispatch();
  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();
  const id = put_id;
  const CommentsGetState = useSelector((state) => state.comments);
  const row = CommentsGetState.CommentsGet?.data;

  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      comentary: nameRu,
      users: nameEn,
    };
    await dispatch(PutComments({ id: id, body }));
    dispatch(GetComments());
    HandleClose();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );

  const brandsGetState = useSelector((state) => state.users);
  const rows = brandsGetState.UserGet?.data;

  const options = [];

  rows.map((elem) =>
    options.push({
      label: elem.surname + " " + elem.name,
      value: elem.id,
    })
  );

  const HandelChange = (e) => {
    setNameEn(e);
  };

  return (
    <DrawerCommon
      title={"Изменить комментарий"}
      open={Open}
      onClose={HandleClose}
    >
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div className="input_wrap">
            {row.map((elem) =>
              elem.id == id ? (
                <>
                  <Row className="row">
                    <Col className="col" lg={8}>
                      <h2>комментарий</h2>
                      <Row className="row">
                        <Col className="col" lg={6}>
                          <h4>описание</h4>
                          <TextArea
                            defaultValue={elem.comentary}
                            onChange={(e) => setNameRu(e.currentTarget.value)}
                            autoSize={{ minRows: 12, maxRows: 14 }}
                          />
                        </Col>
                        <Col className="col" lg={6}>
                          <h4>Выбор пользователя</h4>
                          <SelectCommon
                            defaultValue={
                              elem.users.surname + " " + elem.users.name
                            }
                            options={options}
                            onChange={HandelChange}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </>
              ) : null
            )}
          </div>

          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Изменить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default PutComment;
