import React, { useEffect, useState } from "react";
import HeaderTopCommon from "../../components/common/HeaderTop";
import { WrapperContainer } from "../../style-App";
import { useDispatch } from "react-redux";
import HorizontalSidebar from "../../components/horizontal-sidebar";
import VerticalSidebar from "../../components/vertical-sidebar";
import { GetComments } from "../../redux/comments";
import CommmentsComponent from "../../components/comments";
import { GetUser } from "../../redux/users";

function Comments() {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(GetComments());
    dispatch(GetUser());
  }, []);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  return window.localStorage.getItem("checked") == "false" ? (
    <HorizontalSidebar>
      <WrapperContainer style={{ marginTop: "112px" }}>
        <HeaderTopCommon
          title={"Комментарии"}
          onClick={handleOpen}
          textBtn={"Добавить комментарий"}
        />
        <CommmentsComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </HorizontalSidebar>
  ) : (
    <VerticalSidebar>
      <WrapperContainer>
        <HeaderTopCommon
          title={"Комментарии"}
          onClick={handleOpen}
          textBtn={"Добавить комментарий"}
        />
        <CommmentsComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </VerticalSidebar>
  );
}
export default Comments;
