import React from "react";
import PostComments from "./post";
import CommentsTable from "./table";
import { useDispatch } from "react-redux";
import { useState } from "react";
import { DeleteComments, GetComments } from "../../redux/comments";
import PutComment from "./put";

function CommmentsComponent({ open, handleClose }) {
  const dispatch = useDispatch();
  const [adminId, setAdminId] = useState();
  const [openDelete, setOpenDelete] = useState(false);
  const handleCloseDelete = () => setOpenDelete(false);

  const handleDeleteModal = (e) => {
    setOpenDelete(true);
  };

  const HandleDelete = async (e) => {
    await dispatch(DeleteComments(e.currentTarget.id));
    dispatch(GetComments());
    handleCloseDelete();
  };

  const [openPut, setOpenPut] = useState(false);

  const onClosePut = () => {
    setOpenPut(false);
  };

  const handlePutModal = async (e) => {
    setOpenPut(true);
    setAdminId(e.currentTarget.id);
  };
  return (
    <>
      <PostComments Open={open} HandleClose={handleClose} />
      <CommentsTable
        onClickDelete={handleDeleteModal}
        HandleDelete={HandleDelete}
        handlePutModal={handlePutModal}
      />
      <PutComment put_id={adminId} Open={openPut} HandleClose={onClosePut} />
    </>
  );
}

export default CommmentsComponent;
