import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { Image, Input, Spin } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import "./styles.css";
import { BrandPost, BrandUpload } from "../../../redux/brands";
import TextArea from "antd/es/input/TextArea";
import { PostComments } from "../../../redux/comments";

function PostComment({ Open, HandleClose }) {
  const dispatch = useDispatch();
  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();

  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      comentary: nameRu,
      users: nameEn,
    };
    await dispatch(PostComments(body));
    window.location.reload();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );

  const brandsGetState = useSelector((state) => state.users);
  const rows = brandsGetState.UserGet?.data;

  const options = [];

  rows.map((elem) =>
    options.push({
      label: elem.surname + " " + elem.name,
      value: elem.id,
    })
  );

  const HandelChange = (e) => {
    setNameEn(e);
  };

  return (
    <DrawerCommon title={"Добавить бренд"} open={Open} onClose={HandleClose}>
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div className="input_wrap">
            <Row className="row">
              <Col className="col" lg={8}>
                <h2>комментарий</h2>
                <Row className="row">
                  <Col className="col" lg={6}>
                    <h4>описание</h4>
                    <TextArea
                      onChange={(e) => setNameRu(e.currentTarget.value)}
                      autoSize={{ minRows: 12, maxRows: 14 }}
                    />
                  </Col>
                  <Col className="col" lg={6}>
                    <h4>Выбор пользователя</h4>
                    <SelectCommon options={options} onChange={HandelChange} />
                  </Col>
                </Row>
              </Col>
            </Row>
          </div>

          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Добавить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default PostComment;
