import React, { useEffect, useState } from "react";
import Cookies from "universal-cookie";
import { Tooltip, Dropdown, Space, Switch } from "antd";

import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  UsergroupAddOutlined,
  UserAddOutlined,
  AppstoreOutlined,
  ShoppingCartOutlined,
  FileImageOutlined,
  LoginOutlined,
  CommentOutlined,
  BellOutlined,
  QuestionCircleOutlined,
  UserOutlined,
  SettingOutlined,
  ClusterOutlined,
  ContainerOutlined,
  DatabaseOutlined,
  LayoutOutlined,
  UserSwitchOutlined,
  ShoppingOutlined,
  BoxPlotOutlined,
  BorderOuterOutlined,
  PhoneOutlined,
} from "@ant-design/icons";
import { useSelector, useDispatch } from "react-redux";
import { useLocation } from "react-router-dom";
import { NavLink, useNavigate } from "react-router-dom";
import { Layout, Menu, theme } from "antd";
import "./styles.css";
import logo from "../../assets/image/logos.png";
import { padding } from "@mui/system";
const { Header, Sider, Footer, Content } = Layout;

const VerticalSidebar = ({ children, setIsChecked }) => {
  const text = <span>Выйти</span>;
  const dispatch = useDispatch();
  const [collapsed, setCollapsed] = useState(false);
  const cookies = new Cookies();
  const navigate = useNavigate();
  const HandleLogout = () => {
    cookies.remove("token");
    setTimeout(() => {
      navigate("/");
      window.location.reload();
    }, "500");
  };

  const id = cookies.get("adminId");

  const adminGetState = useSelector((state) => state.admin);
  const rows = adminGetState.adminGet?.data;

  const filterData = rows.filter((elem) => elem.id == id);

  const onChange = () => {
    window.localStorage.setItem("checked", "false");
    window.location.reload();
  };
  const {
    token: { colorBgContainer },
  } = theme.useToken();

  const items = [
    {
      label: (
        <Tooltip title={text}>
          <button className="tool_btns" onClick={HandleLogout}>
            <span>Выйти</span>
            <LoginOutlined />
          </button>
        </Tooltip>
      ),
      key: "0",
    },
  ];

  return (
    <Layout style={{ height: "100vh", position: "relative" }}>
      <Sider
        style={{ backgroundColor: "#1a71e3" }}
        width={240}
        trigger={null}
        collapsible
        collapsed={collapsed}
      >
        {rows.map((elem) =>
          elem.role == "SuperAdmin" ? (
            <>
              <Menu
                theme="dark"
                mode="inline"
                defaultSelectedKeys={["1"]}
                items={[
                  {
                    key: "1",
                    icon: <UserAddOutlined />,
                    label: (
                      <NavLink to={"/adminadd"}>
                        <span class="title">Админ</span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "2",
                    icon: <ShoppingOutlined />,
                    label: (
                      <>
                        <span class="title">Продукты</span>
                      </>
                    ),
                    children: [
                      {
                        key: "3",
                        label: <NavLink to={"/products"}>Все продукты</NavLink>,
                      },
                      {
                        key: "4",
                        label: <NavLink to={"/brands"}>бренд</NavLink>,
                      },
                      {
                        key: "5",
                        label: <NavLink to={"/category"}>Категория</NavLink>,
                      },
                    ],
                  },
                  {
                    key: "6",
                    icon: <BoxPlotOutlined />,
                    label: (
                      <NavLink to={"/banner"}>
                        <span class="title">Баннер</span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "7",
                    icon: <BorderOuterOutlined />,
                    label: (
                      <NavLink to={"/neworders"}>
                        <span class="title">Новые заказы</span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "8",
                    icon: <ContainerOutlined />,
                    label: (
                      <NavLink to={"/gottenorders"}>
                        <span class="title">Заказы </span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "9",
                    icon: <UserSwitchOutlined />,
                    label: (
                      <NavLink to={"/users"}>
                        <span class="title">Заявки </span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "9",
                    icon: <CommentOutlined />,
                    label: (
                      <NavLink to={"/comments"}>
                        <span class="title">Комментарии </span>
                      </NavLink>
                    ),
                  },
                ]}
                style={{ backgroundColor: "#ED254E" }}
              />
            </>
          ) : (
            <>
              <Menu
                theme="dark"
                mode="inline"
                defaultSelectedKeys={["1"]}
                items={[
                  {
                    key: "2",
                    icon: <ShoppingOutlined />,
                    label: (
                      <>
                        <span class="title">Продукты</span>
                      </>
                    ),
                    children: [
                      {
                        key: "3",
                        label: <NavLink to={"/products"}>Все продукты</NavLink>,
                      },
                      {
                        key: "4",
                        label: <NavLink to={"/brands"}>бренд</NavLink>,
                      },
                      {
                        key: "5",
                        label: <NavLink to={"/category"}>Категория</NavLink>,
                      },
                    ],
                  },
                  {
                    key: "6",
                    icon: <BoxPlotOutlined />,
                    label: (
                      <NavLink to={"/banner"}>
                        <span class="title">Баннер</span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "7",
                    icon: <BorderOuterOutlined />,
                    label: (
                      <NavLink to={"/neworders"}>
                        <span class="title">Новые заказы</span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "8",
                    icon: <ContainerOutlined />,
                    label: (
                      <NavLink to={"/gottenorders"}>
                        <span class="title">Заказы </span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "9",
                    icon: <UserSwitchOutlined />,
                    label: (
                      <NavLink to={"/users"}>
                        <span class="title">Заявки </span>
                      </NavLink>
                    ),
                  },
                  {
                    key: "9",
                    icon: <CommentOutlined />,
                    label: (
                      <NavLink to={"/comments"}>
                        <span class="title">Комментарии </span>
                      </NavLink>
                    ),
                  },
                ]}
                style={{ backgroundColor: "#ED254E" }}
              />
            </>
          )
        )}
        <div className="logo_wrapp">{/* <img src={logo} alt="" /> */}</div>
      </Sider>
      <Layout className="site-layout">
        <Header
          style={{
            padding: 0,
            background: "#ED254E",
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <div className="logos">
            {React.createElement(
              collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
              {
                className: "trigger",
                onClick: () => setCollapsed(!collapsed),
              }
            )}
          </div>
          <div className="left">
            <div className="icons"></div>
            <div className="profile">
              <Dropdown
                style={{ marginLeft: "10px" }}
                menu={{
                  items,
                }}
                trigger={["click"]}
              >
                <a onClick={(e) => e.preventDefault()}>
                  <Space>
                    <SettingOutlined />
                    <span className="drop_span">Настройки</span>
                  </Space>
                </a>
              </Dropdown>
              <UserOutlined className="user-icon" />
              <div className="profile-right">
                {filterData.map((elem) => (
                  <>
                    <p className="profile_email">{elem.admin_surname}</p>
                    <p className="profile_name">{elem.admin_name}</p>
                  </>
                ))}
              </div>
            </div>
          </div>
        </Header>

        <Content
          style={{
            margin: "24px 16px",
            padding: 24,
            overflowY: "auto",
          }}
        >
          {children}
        </Content>
        <Footer
          style={{
            textAlign: "center",
            background: "transparent",
            color: "#ED254E",
            fontWeight: "500",
            padding: "10px 20px",
          }}
        >
          ©2023 Created by Abdulloh
        </Footer>
      </Layout>
    </Layout>
  );
};
export default VerticalSidebar;
