import * as React from "react";
import { useSelector } from "react-redux";
import TableCommon from "../../common/table";
import "./styles.css";
import { Image, Popover, Space } from "antd";

export default function CommentsTable({ handlePutModal, HandleDelete }) {
  const brandsGetState = useSelector((state) => state.comments);
  const rows = brandsGetState.CommentsGet?.data;

  const data = [];
  rows?.map((elem, index) => {
    data.push({
      data: index + 1,
      key: elem.id,
      Комментарий: elem.comentary,
      Ии: elem.users.surname + " " + elem.users.name,
      Удалить: (
        <div className="boxx">
          <div className="btn-wraps">
            <button onClick={handlePutModal} id={elem.id}>
              <i id={elem.id} class="bx bx-message-square-edit"></i>
            </button>
          </div>
          <Space wrap>
            <Popover
              trigger="click"
              placement="rightBottom"
              content={
                <div className="content_delete_box">
                  <p>Вы уверены, что хотите удалить эту админ?</p>
                  <div className="btn_wrap_delete">
                    <button
                      onClick={HandleDelete}
                      id={elem.id}
                      className="yes_btn"
                    >
                      Удалить
                    </button>
                  </div>
                </div>
              }
              title={
                <div className="delete_box">
                  <i class="bx bxs-error-circle"></i>
                  <span>
                    Удалите админ <span>{elem.name}</span>
                  </span>
                </div>
              }
            >
              <div className="btn-wrap">
                <button id={elem.id}>
                  <i class="bx bxs-trash"></i>
                </button>
              </div>
            </Popover>
          </Space>
        </div>
      ),
    });
  });

  const columns = [
    {
      title: "№",
      dataIndex: "data",
      key: "data",
      width: 50,
      align: "center",
    },
    {
      title: "Комментарий",
      dataIndex: "Комментарий",
      key: "Комментарий",
    },
    {
      title: "Имя и фамилия",
      dataIndex: "Ии",
      key: "Ии",
    },
    {
      title: "Удалить",
      dataIndex: "Удалить",
      key: "Удалить",
      align: "center",
    },
  ];
  return (
    <TableCommon
      bordered
      columns={columns}
      data={data}
      pagination={false}
      scroll={{
        y: 330,
      }}
    />
  );
}
